import React from "react";
import {Box, Menu, MenuItem, Tooltip} from "@mui/material";
import {IHeroPack} from "../types/IHeroPack";
import {HeroModernIcon} from "./HeroModernIcon";
import {HeroData} from "../types";
import MoreVertIcon from '@mui/icons-material/MoreVert';
import LinkIcon from '@mui/icons-material/Link';

function isNN(value:any) {
    return value!==undefined && value !== null
}

function nvl(value:any,newvalue:any) {
    if (isNN(value)) {
        return value
    } else {
        return newvalue
    }
}
function formatK(value:number | undefined | null) {
    if (value!==undefined && value !== null) {
        let result=""
        if (value>1000000) {
            result=(value/1000000).toFixed(1)+"M"
        } else if (value>1000) {
            result=(value/1000).toFixed(1) + "k"
        } else {
            result=""+~~value
        }
        return result //.toString().replace(/\B(?=(\d{3})+(?!\d))/g, " ")+suffix
    } else {
        return "?"
    }
}

interface IProps {
    heroPack: IHeroPack,
    heroProbs: Map<number, number>,
    onHeroSelect: any,
    selectedHeroes: number[],
    additionalClasses: string[],
    currentPack?: IHeroPack,
    currentInAttack?: boolean,
    gMode?: string
}

interface IState {
    anchorEl: any,
}

export class SelectedHeroPack extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            anchorEl: null,
        }

        this.seeCounterPacks = this.seeCounterPacks.bind(this)
        this.handleMenuClick = this.handleMenuClick.bind(this)
        this.handleMenuClose = this.handleMenuClose.bind(this)
    }

    renderHeroIcons() {
        this.props.heroPack.hIds.sort((a, b) => {
            return (HeroData.getHeroById(a)?.battleOrder || 0) - (HeroData.getHeroById(b)?.battleOrder || 0)
        })
        const items: any = []
        for (let i = 0; i < 5; i++) {
            const hId = this.props.heroPack.hIds[i]
            items.push(<HeroModernIcon key={i} additionalClasses={["selectedHeroPackIcons"]} heroId={hId}
                                       heroProb={this.props.heroProbs.get(hId) || 0}
                                       onClick={this.props.onHeroSelect}/>)
        }
        return items
    }

    copyLink() {
        navigator.clipboard.writeText(HeroData.translateHeroIdsArray(this.props.heroPack.hIds))
    }

    seeCounterPacks(e: any) {

    }

    handleMenuClick(e: any) {
        this.setState({anchorEl: e.currentTarget})
    }

    handleMenuClose(e: any) {
        this.setState({anchorEl: null})
    }

    renderMenuItems() {
        const menuItems :JSX.Element[] = []
        let key = 0
        menuItems.push(<MenuItem key={key++} onClick={this.seeCounterPacks}>Посмотреть кто побеждает (не работает)</MenuItem>)
        /*if (this.props.heroIdsA !== undefined && this.props.heroIdsD !== undefined) menuItems.push(<MenuItem key={key++} onClick={this.seeTrainMode}>Add to Train both packs</MenuItem>)*/
        return menuItems
    }

    render() {
        const additionalClasses = "selectedHeroPack " + this.props.additionalClasses.join(" ")
        return (
            <Box className={additionalClasses}>
                {this.props.heroPack?.extendedInfo?.cwr !== undefined && this.props.heroPack?.extendedInfo?.cwr !== null && this.props.heroPack?.extendedInfo?.cc !== undefined && this.props.heroPack?.extendedInfo?.cc !== null &&
                (<Box className={"selectedHeroPackExtendedCurrent"}>
                    <Tooltip title={"Процент побед выбранного пака"}><span><img
                        src={"./trophy_a_w.png"}/><span>{this.props.heroPack.extendedInfo.cwr + "%"}</span></span></Tooltip>
                    <Tooltip title={"Количество побед выбранного пака"}><span><img
                        src={"./attack_w.png"}/><span>{this.props.heroPack.extendedInfo.cc}</span></span></Tooltip>
                </Box>)}
                <Box className={"selectedHeroPackIcons"}>
                    {this.renderHeroIcons()}
                    <Box className={"linkVerticalBar"}>
                        <Box id="demo-positioned-button"
                             aria-controls="demo-positioned-menu"
                             aria-haspopup="true"
                             aria-expanded={this.state.anchorEl ? 'true' : undefined}
                             onClick={this.handleMenuClick}><Tooltip title={"Дополнительная информация"}><MoreVertIcon/></Tooltip></Box>
                        <Tooltip title={"Копировать ссылку на пак"}><LinkIcon onClick={this.copyLink}/></Tooltip>
                    </Box>
                </Box>
                <Menu
                    id="demo-positioned-menu"
                    aria-labelledby="demo-positioned-button"
                    anchorEl={this.state.anchorEl}
                    open={!!this.state.anchorEl}
                    onClose={this.handleMenuClose}
                    anchorOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                    transformOrigin={{
                        vertical: 'top',
                        horizontal: 'right',
                    }}
                >
                    {this.renderMenuItems()}
                </Menu>
                <Box className={"selectedHeroPackExtended"}>
                    <span>Всего:</span>
                    <Tooltip title={"Всего % побед в атаке"}><span><img
                        src={"./trophy_a_w.png"}/><span>{nvl(this.props.heroPack?.extendedInfo?.wra,"?") + "%"}</span></span></Tooltip>
                    <Tooltip title={"Всего побед в атаке"}><span><img
                        src={"./attack_w.png"}/><span>{nvl(this.props.heroPack?.extendedInfo?.ca,"?")}</span></span></Tooltip>
                    <Tooltip title={"Всего % побед в защите"}><span><img
                        src={"./trophy_d_w.png"}/><span>{nvl(this.props.heroPack?.extendedInfo?.wrd,"?") + "%"}</span></span></Tooltip>
                    <Tooltip title={"Всего побед в защите"}><span><img
                        src={"./shield_w.png"}/><span>{nvl(this.props.heroPack?.extendedInfo?.cd,"?")}</span></span></Tooltip>
                </Box>
                {(isNN(this.props.heroPack?.extendedInfo?.hl) || isNN(this.props.heroPack?.extendedInfo?.hd) || isNN(this.props.heroPack?.extendedInfo?.hf) || isNN(this.props.heroPack?.extendedInfo?.hw) || isNN(this.props.heroPack?.extendedInfo?.ha) || isNN(this.props.heroPack?.extendedInfo?.he)) && (
                    <React.Fragment>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>Гидры:</span>
                        <Tooltip title={"Урон гидре тьмы"}><span><img
                            src={"./hdarkness.png"}/><img src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(this.props.heroPack?.extendedInfo?.hd)}</span>
                        <Tooltip title={"Урон гидре света"}><span><img
                            src={"./hlight.png"}/><img src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(this.props.heroPack?.extendedInfo?.hl)}</span>
                    </Box>
                <Box className={"selectedHeroPackExtended"}>
                    <span>Гидры:</span>
                    <Tooltip title={"Урон гидре воды"}><span><img
                        src={"./hwater.png"}/><img src={"./damage.png"}/></span></Tooltip>
                    <span>{formatK(this.props.heroPack?.extendedInfo?.hw)}</span>
                    <Tooltip title={"Урон гидре ветра"}><span><img
                        src={"./hwind.png"}/><img src={"./damage.png"}/></span></Tooltip>
                    <span>{formatK(this.props.heroPack?.extendedInfo?.ha)}</span>
                </Box>
                    <Box className={"selectedHeroPackExtended"}>
                        <span>Гидры:</span>
                        <Tooltip title={"Урон гидре земли"}><span><img src={"./hearth.png"}/><img src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(this.props.heroPack?.extendedInfo?.he)}</span>
                        <Tooltip title={"Урон гидре огня"}><span><img src={"./hfire.png"}/><img src={"./damage.png"}/></span></Tooltip>
                        <span>{formatK(this.props.heroPack?.extendedInfo?.hf)}</span>
                    </Box></React.Fragment>)}
            </Box>
        )
    }
}